// components/editor/Editor.tsx

import { Mention } from "@/plugins/MentionsPlugin";
import { LexicalComposer } from "@lexical/react/LexicalComposer";
import { JSX } from "react";
import { SharedHistoryContext } from "./context/SharedHistoryContext";
import { ToolbarContext } from "./context/ToolbarContext";
import LexicalEditorInner, { EditorPluginConfig, InnerEditorProps } from "./EditorInner";
import PlaygroundNodes from "./nodes/PlaygroundNodes";
import { TableContext } from "./plugins/TablePlugin";
import PlaygroundEditorTheme from "./themes/PlaygroundEditorTheme";
import { $prepopulatedRichText } from "./utils/editorConfig";

const defaultInitialConfig = {
  namespace: "Editor",
  theme: PlaygroundEditorTheme,
  nodes: [...PlaygroundNodes],
  onError: (e: Error) => {
    console.error(e);
    throw e;
  },
  editorState: $prepopulatedRichText,
};

export interface EditorWrapperProps extends Omit<InnerEditorProps, "editor"> {
  initialConfig?: Partial<
    Parameters<typeof LexicalComposer>[0]["initialConfig"]
  >;
}

export default function Editor({
  initialConfig = {},
  plugins,
  ...props
}: EditorWrapperProps): JSX.Element {
  const mergedConfig = {
    ...defaultInitialConfig,
    ...initialConfig,
  };

  return (
    <LexicalComposer initialConfig={mergedConfig}>
      <SharedHistoryContext>
        <TableContext>
          <ToolbarContext>
            <LexicalEditorInner plugins={plugins} {...props} />
          </ToolbarContext>
        </TableContext>
      </SharedHistoryContext>
    </LexicalComposer>
  );
}
